'use strict'; // Convenience functions for extracting non-subsystem-specific data from protobuf states

function getSubsystems(state) {
  if (!state) return null;
  return state.getSubsystems();
}

function getLatestState(states) {
  if (!states.length) return null;
  return states[states.length - 1];
}

var emptyTimes = {
  start: 0,
  end: 0,
  duration: 0
};

function getStateTimes(state) {
  if (!state) return emptyTimes;
  var end = state.getInstantTs();
  var duration = state.getSnapshotDurationMs();
  var start = state.getStartTs();
  return {
    // Can't rely on introspection sending complete data
    // Try to fill in any gaps with whatever we have
    start: start || end - duration || 0,
    end: end || start + duration || 0,
    duration: duration || end - start || 0
  };
}

function getStateRangeTimes(states) {
  if (!states || !states.length) return emptyTimes;
  var start = states[0].getStartTs();
  var end = states[states.length - 1].getInstantTs();
  var duration = end - start;
  return {
    start: start,
    end: end,
    duration: duration
  };
}

function getStateIndex(states, timestamp) {
  return states.findIndex(function (state) {
    return getStateTimes(state).end === timestamp;
  });
}

module.exports = {
  getLatestState: getLatestState,
  getSubsystems: getSubsystems,
  getStateRangeTimes: getStateRangeTimes,
  getStateTimes: getStateTimes,
  getStateIndex: getStateIndex
};